<template>
  <slick ref="slick" :options="slickOptions" class="slider-news-spikers">
    <state-item v-for="i in 10" v-bind:key="i">
      <div class="states-wrapper">
        <a
          href="/"
          :style="{
            background: 'url(' + require('@/assets/img/podpiska1.png') + ')'
          }"
          class="spiker-items-img"
        ></a>
        <h4><router-link to="/">Игорь Манн</router-link></h4>
        <tags :tags="['4 модуля', '7 видео']" />
        <img class="img-flag" src="@/assets/img/italy.svg" alt="IT" />
      </div>
    </state-item>
  </slick>
</template>

<script>
import Slick from "vue-slick";
import StateItem from "@/components/page/slick/StateItem";
import Tags from "@/components/page/items/Tags";

export default {
  name: "Speakers",
  components: { StateItem, Slick, Tags },
  data() {
    return {
      slickOptions: {
        infinite: true,
        dots: false,
        arrows: true,
        slidesToShow: 4,
        slidesToScroll: 1,
        rows: 0,
        prevArrow:
          "<div class='slick-prev-states'><img src='" +
          this.arrowUrl("right") +
          "' alt='' /></div>",
        nextArrow:
          "<div class='slick-next-states'><img src='" +
          this.arrowUrl("left") +
          "' alt='' /></div>",
        responsive: [
          {
            breakpoint: 760,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 1
            }
          },
          {
            breakpoint: 450,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      }
    };
  },
  methods: {
    arrowUrl(arrow) {
      return require("@/assets/img/arrow-" + arrow + ".svg");
    },
    next() {
      this.$refs.slick.next();
    },
    prev() {
      this.$refs.slick.prev();
    },
    reInit() {
      this.$nextTick(() => {
        this.$refs.slick.reSlick();
      });
    }
  }
};
</script>

