<script>
import $ from "jquery";
import MainMenu from "@/components/page/menu/MainMenu";
import SocialMenu from "@/components/page/menu/SocialMenu";
export default {
  name: "SiteHeader",
  components: { SocialMenu, MainMenu },
  methods: {
    searchToggle() {
      let searchInput = $(".search-input");
      if (searchInput.css("display") === "none") {
        $(".top-menu").fadeOut();
        searchInput.fadeIn();
        $(".close-search").fadeIn();
        $(".top-search").css("right", "400px");
      } else {
        searchInput.fadeOut();
        $(".close-search").fadeOut();
        $(".top-search").css("right", "330px");
        $(".top-menu").fadeIn();
        $(".search-result").fadeOut();
      }
    },
    searchInput() {
      if ($(".search-input").val()) {
        $(".search-result").fadeIn();
      } else {
        $(".search-result").fadeOut();
      }
    }
  }
};
</script>

