<template>
  <div class="wrapper-registration">
    <div class="left-login"></div>
    <div class="right-registration">
      <router-link class="services-other" to="/"
        ><img src="@/assets/img/arrow-round-back.svg" />
        <span>Назад</span></router-link
      >
      <br /><br /><br /><br />
      <div class="logo-reg">
        <router-link to="/"><img src="@/assets/img/logo.svg"/></router-link>
      </div>
      <div id="reg-window1">
        <h2>Войти</h2>
        <p class="reg-pre">
          Войдите или зарегистрируйтесь для доступа к контенту
        </p>
        <form @submit.prevent="auth">
          <div id="reg-step1">
            <div class="input-reg-area">
              <input
                v-model="formData.email"
                type="text"
                placeholder="E-mail или телефон"
              />
            </div>
            <div class="input-reg-area">
              <input
                v-model="formData.password"
                type="password"
                placeholder="Пароль"
              />
              <a href="#"><i class="fas fa-eye"></i></a>
            </div>
            <a class="remove-pass" href="#">Забыли пароль?</a>
            <div class="input-reg-submit">
              <input type="submit" value="Войти" />
            </div>
          </div>
        </form>
        <p class="login-reg">
          Нет аккаунта?
          <router-link to="/signup">Зарегистрируйтесь</router-link>
        </p>
      </div>
      <div style="display: none;" id="reg-window2">
        <h2>Восстановление пароля</h2>
        <p class="reg-pre">Введите свой E-mail, на него мы вышлем письмо</p>
        <form>
          <div id="reg-step1">
            <div class="input-reg-area">
              <input type="text" placeholder="E-mail" />
            </div>
            <div class="input-reg-submit">
              <input id="new-password" type="button" value="Отправить" />
            </div>
          </div>
        </form>
        <p class="login-reg">
          Вернуться к <router-link to="/login">входу</router-link>
        </p>
      </div>
      <div style="display: none;" id="reg-window3">
        <h2>Восстановление пароля</h2>
        <p class="reg-pre">Введите новый пароль</p>
        <form>
          <div id="reg-step1">
            <div class="input-reg-area">
              <input type="password" placeholder="E-mail" />
              <a href="#"><i class="fas fa-eye"></i></a>
            </div>
            <div class="input-reg-area">
              <input type="password" placeholder="E-mail" />
              <a href="#"><i class="fas fa-eye"></i></a>
            </div>
            <div class="input-reg-submit">
              <input type="submit" value="Войти" />
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      formData: {
        email: null,
        password: null
      }
    };
  },
  methods: {
    auth() {
      this.$auth.login({
        data: this.formData,
        success() {
          alert("OK!");
          const redirect = this.$auth.redirect();
          const redirectTo = redirect ? redirect.from.name : "Parent";

          this.$router.push({ name: redirectTo });
        },
        fetchUser: true,
        rememberMe: true
      });
    }
  }
};
</script>

