<template>
  <div class="news-tovar-items-inner wow fadeInUp">
    <a class="tovar-items-img">
      <video
        height="180"
        ref="videoPlayer"
        :id="'video_' + data.video.id"
        class="video-js vjs-default-skin"
      />
      <div class="hidden-video">
        <div class="absolute-vote">
          <i class="fas fa-star"></i> <span>{{ data.video.vote }}</span>
          <!--<img class="video-items-icon" src="@/assets/img/video.svg" />-->
        </div>
        <div class="bottom-videos">
          <div class="videos-clock">
            <i class="far fa-clock"></i> <span>{{ data.video.length }}</span>
          </div>
          <div class="videos-views">
            <img src="@/assets/img/eye.svg" alt="views" />
            <span>{{ data.video.views }}</span>
          </div>
        </div>
      </div>
    </a>
    <tags :tags="data.tags" />
    <h4>
      <a href="/">{{ data.name }}</a>
    </h4>
    <div class="tovar-items-status">
      <span :class="['items-status-circle', 'green']"></span>
      <span class="items-status-text">{{ data.status }}</span>
    </div>
  </div>
</template>

<script>
import videojs from "video.js";
import "videojs-youtube";
import Tags from "@/components/page/items/Tags";

window.videojs = videojs;
require("@xiaoyexiang/videojs-resolution-switcher-v7");

export default {
  name: "VideoTile",
  components: { Tags },
  data() {
    return {
      player: null
    };
  },
  props: {
    data: {
      type: Object,
      default() {
        return {
          tags: ["", ""],
          name: "",
          status: "",
          video: {
            length: "",
            views: 0,
            vote: "",
            id: 1,
            sources: [
              {
                src: "",
                type: "",
                label: "",
                res: 480
              }
            ]
          }
        };
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this.player = videojs(this.$refs.videoPlayer, {
        controls: true,
        dataSetup: {},
        sources: this.data.video.sources
      }).videoJsResolutionSwitcher();
    });
  },
  beforeDestroy() {
    if (this.player) {
      this.player.dispose();
    }
  }
};
</script>

<style lang="scss" scoped>
@import "~video.js/dist/video-js.css";
@import "~@xiaoyexiang/videojs-resolution-switcher-v7/lib/videojs-resolution-switcher-v7.css";
</style>
